            </div>
            <Pagination :pages="pages" :classification="key"></Pagination>
        </section>

        <!--测试数据-->
        <section id="content">
            <div class="container">
                <div class="row">
                    <div class="row col-md-9">
                        <article class="item">
                            <div class="row">
                                <div class="col-md-9">
                                    <h2>
                                        <a href="article/container_io/1.pdf">MultiLanes: Providing Virtualized Storage for OS-level Virtualization on Many Cores</a>
                                    </h2>
                                    <p>
                                        This paper presents MultiLanes, a virtualized storage system for OS-level virtualization on many cores.
                                        MultiLanes builds an isolated I/O stack on top of a virtualized storage device for each VE to eliminate contention on kernel data structures and locks between them,
                                        thus scaling them to many cores.
                                        The evaluation of their prototype system built for Linux container <code>(LXC)</code> on a 16-core machine with a RAM disk demonstrates MultiLanes outperforms Linux by up to 11.32X and 11.75X in micro- and macro-benchmarks, and exhibits nearly linear scalability
                                    </p>
                                </div>
                                <div class="col-md-3">
                                    <a href="article/container_io/1.pdf">
                                        <img src="picture/container_io/1.png"/>
                                    </a>
                                </div>
                            </div>
                        </article>
                        <article class="item">
                            <div class="row">
                                <div class="col-md-9">
                                    <h2>
                                        <a href="article/container_io/3.pdf">Performance Analysis of Containerized Applications on Local and Remote Storage
                                        </a>
                                    </h2>
                                    <p>
                                        This paper presents a characterization of the performance
                                        impact among a wide variety of the available storage options
                                        for deploying Docker containers and provide the configuration
                                        options to best utilize the high performance SSDs.The study provide experimental results on scaling
                                        the performance of <code>NVMf</code> to multiple nodes and present the
                                        challenges and projections for future storage system design.
                                    </p>
                                </div>
                                <div class="col-md-3">
                                    <a href="article/container_io/3.pdf">
                                        <img src="picture/container_io/3.png"/>
                                    </a>
                                </div>
                            </div>
                        </article>
                        <article class="item">
                            <div class="row">
                                <div class="col-md-9">
                                    <h2>
                                        <a href="article/container_io/4.pdf">Docker Characterization on High Performance SSDs
                                        </a>
                                    </h2>
                                    <p>This paper provides an extensive characterization of
                                        Docker storage subsystem in the presence of high performance
                                        SSDs.And demonstrates that from a performance perspective,
                                        all storage backends for Docker are not created equal. In order
                                        to utilize the device to near saturation, it is better to use overlay
                                        or aufs backends.The study show that the Docker volume is
                                        able to utilize these SSDs as efficiently as natively running
                                        applications.
                                    </p>
                                </div>
                                <div class="col-md-3">
                                    <a href="article/container_io/4.pdf">
                                        <img src="picture/container_io/4.png"/>
                                    </a>
                                </div>
                            </div>
                        </article>
                        <article class="item">
                            <div class="row">
                                <div class="col-md-9">
                                    <h2>
                                        <a href="article/container_io/5.pdf">Multi-Tenant I/O Isolation with Open-Channel SSDs</a>
                                    </h2>
                                    <p>
                                        Latency guarantees remain a challenge due
                                        to the unpredictability intrinsic to the design of current SSDs
                                        and the narrow interfaces available to the host. This paper proposes
                                        the use of Open-Channel SSDs to partition the device into its
                                        physical units, accessible in parallel by the host, and mapped
                                        onto tenants via traditional block devices in order to provide
                                        <span>I/O</span> isolation. They describe their architecture and provide an
                                        experimental evaluation to show the benefits.
                                    </p>
                                </div>
                                <div class="col-md-3">
                                    <a href="article/container_io/5.pdf">
                                        <img src="picture/container_io/5.png"/>
                                    </a>
                                </div>
                            </div>
                        </article>
                        <article class="item">
                            <div class="row">
                                <div class="col-md-9">
                                    <h2>
                                        <a href="article/container_io/6.pdf">OC-Cache: An Open-channel SSD Based Cache for Multi-Tenant Systems</a>
                                    </h2>
                                    <p>
                                        This paper proposes OC-Cache, an
                                        open-channel SSD cache framework which utilizes SSD’d internal
                                        parallelism to adaptively allocate cache to tenants for both good
                                        performance isolation and high SSD utilization. In particular,
                                        OC-Cache uses a tenant’s miss ratio curve to determine the
                                        amount of cache space allocation and where the allocation is and dynamically manages
                                        cache space according to the workload characteristics. Experiments show that OC-Cache significantly reduces interference
                                        among tenants, and maintains high utilization of the <code>SSD cache</code>.
                                    </p>
                                </div>
                                <div class="col-md-3">
                                    <a href="article/container_io/6.pdf">
                                        <img src="picture/container_io/6.png"/>
                                    </a>
                                </div>
                            </div>
